.FeaturedProject {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
}

.FeaturedProjectLeft {
    display: flex;
    flex-direction: column;
    width: 55%;
    justify-content: center;
    align-content: center;
}

.FeaturedProjectLeft h5 {
    color: #E27D60;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}

.FeaturedProjectLeft hr {
    width: 75px;
    float: left;
    margin-left: 100px;
    border: 1px solid #E27D60;
}

.featuredProject-title {
    display: flex;
    flex-direction: row;
}

.FeaturedProjectLeft h1 {
    font-size: 2.5em;
    text-align: left;
    margin-left: 100px;
    color: white;
    margin-top: 0px;
    line-height: 10px;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 1);
}

.FeaturedProjectLeft p {
    margin-left: 100px;
    font-size: 1em;
    text-align: justify;
    max-width: 460px;
    color: white;
    margin-top: 40px;
    margin-bottom: 40px;
    line-height: 157.2%;
}

.FeaturedProjectRight {
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.FeaturedProjectRight img {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 500px;
    margin-right: 100px;
    height: auto;
}